import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import { addcourse, getcourseid, updatecourse } from "../../api/user";
// import './Style.css'
const UserForm = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [videoLink, setVideoLink] = useState("");
  const [featuredImage , setfeaturedImage] = useState("")
  const [id , setID]= useState("")
  const actionType = location.state?.actionType;
  let courseId = location.state?.courseId;
//   userId =  id;
  console.log(courseId)
  useEffect(() => {
    if (actionType === "edit" ) {
     
      console.log(courseId)
    
       getcourseid(courseId)
          .then((response) => {
            const user = response;
            console.log(user)
            setTitle(user.title);
            setDescription(user.description);
            setVideoLink(user.videoLink);
            setfeaturedImage(user.featuredImage)
          })
          .catch((error) => {
            console.log(error);
          });
      
    }
  }, [actionType, location]);

  const handleSubmit = (event) => {
    event.preventDefault();

    const user = {
        id: courseId,
        title: title,
        description: description,
        videoLink: videoLink,
    };

    if (actionType === "edit") {
        let courseId = location.state?.courseId;
        updatecourse(courseId , title , description , videoLink , featuredImage)
          .then((response) => {
            console.log("Course updated successfully:", response.data);
            navigate("../grid");
          })
          .catch((error) => {
            console.log(error);
          });
      
    } else if (actionType === "add") {
      addcourse( title , description , videoLink , featuredImage)
        .then((response) => {
          console.log("User added successfully:", response.data);
          navigate("../grid");
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  const handleCancel = () => {
    navigate("../grid");
  };

  return (
    <div className="user-form-container caliber-font">
      <form onSubmit={handleSubmit} className="user-form ">
        <h2 className="form-title">
          {actionType === "edit" ? "Edit Course" : "Add Course"}
        </h2>
        <div className="form-group">
          <label htmlFor="Title">Title:</label>
          <input
            type="text"
            id="title"
            className="form-input  "
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="Description">Description:</label>
          <input
            type="text"
            id="description"
            className="form-input"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="videolink">Video link:</label>
          <input
            type="text"
            id="videolink"
            className="form-input"
            value={videoLink}
            onChange={(e) => setVideoLink(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="Title">featuredImage:</label>
          <input
            type="text"
            id="featureImage"
            className="form-input  "
            value={featuredImage}
            onChange={(e) => setfeaturedImage(e.target.value)}
          />
        </div>
        <div className="form-buttons">
          <button type="submit" className="submit-button ">
            {actionType === "edit" ? "Save" : "Add"}
          </button>
          <button type="button" className="cancel-button" onClick={handleCancel}>
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

export default UserForm;
